import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { LoginWrapper,LoginBox,Input,Button} from './style';
import { actionCreators } from './store';
import { Redirect } from 'react-router-dom';

class Login extends PureComponent {
	
	render() {
		
		const { logindata } = this.props;

		if(!logindata){
			return(
				<LoginWrapper>
					<LoginBox>
					 <Input placeholder='' innerRef={(input) => {this.account = input}}/>
					 <Input placeholder='' type='password' innerRef={(input) => {this.password = input}}/>
					 <Button onClick={() =>{this.props.login(this.account,this.password)}}></Button>
					</LoginBox>
				</LoginWrapper>
			)
		}else{
			return <Redirect to='/' />
		}
		
		
		
	}
}

const mapState = (state) => ({
	logindata:state.getIn(['login','login'])
})


const mapDispatch = (dispatch) => ({
	login(accountElement,passwordElement){
		dispatch(actionCreators.login(accountElement.value,passwordElement.value));
	}
}) 

export default connect(mapState, mapDispatch)(Login);


